<template>
  <div class="account">
    <div class="cut">
      <router-link to="login" replace :class="{active: isactive === 'login'}"  exact>登 录</router-link>
      <router-link to="register" replace :class="{active: isactive === 'register'}">注 册</router-link>
    </div>
    <div class="main">
      <router-view />
    </div>
  </div>
</template>

<script>
export default {
  name: "Account",
  computed: {
    isactive() {
      let active = "";
      if (this.$route.name === "login") {
        active = "login";
      } else if (this.$route.name === "register") {
        active = "register";
      }
      return active;
    }
  }
};
</script>

<style lang="less" scoped>
  .account{
    margin-top: 60px;
    padding: 30px;
    background: rgba(255,255,255,0.02);
    .cut {
      padding: 30px 0 24px;
      font-size: 24px;
      display: flex;
      justify-content: center;
      border-bottom: 1px dashed #808081 ;
      a {
        color: #78787d;
        padding: 0 50px;
      }
      .active {
        color: @warn;
      }
    }
    .main {
      display: flex;
      justify-content: center;
      padding-top: 80px;
      transform: translate(-70px);
    }
  }
</style>
